import { withScriptjs, withGoogleMap, GoogleMap, Marker } from "react-google-maps"

const Maps = withScriptjs(withGoogleMap((props) =>
  <GoogleMap
    defaultZoom={8}
    defaultCenter={{ lat: 34.7556096, lng: -92.3465631 }}
  >
    {props.isMarkerShown && <Marker position={{ lat: 34.755, lng: -92.346 }} />}
  </GoogleMap>
))

export default Maps;